* {
  box-sizing: border-box;
  word-break: keep-all;
  color: inherit;
  text-overflow: ellipsis;
  margin: 0;
  padding: 0;
}

:focus {
  outline: none;
  transition: box-shadow .2s ease-in-out;
}

html {
  scroll-behavior: smooth;
  font-size: calc(25% + .067vw);
}

body {
  font-family: Poppins, sans-serif;
  font-size: 3.25rem;
}

button, select {
  cursor: pointer;
  background: none;
  border: none;
